import { useDispatch, useSelector } from "react-redux"
import { onAddNewEvent, onDeleteEvent, onLoadEvents, onRefreshEvent, onSetActiveEvent, onUpdateEvent } from "../store"
import { calendarApi } from '../api'
import { convertStringToDate } from "../helpers/convertStringToDate";
import Swal from 'sweetalert2'

export const useCalendarStore = () => {

    const dispatch = useDispatch();
    const { events, activeEvent } = useSelector(state => state.calendar);
    const { user } = useSelector(state => state.auth);

    const setActiveEvent = (calendarEvent) => {

        dispatch(onSetActiveEvent(calendarEvent))
    }

    const startSavingEvent = async (calendarEvent) => {

        try {

            if (calendarEvent.id) {

                await calendarApi.put(`/events/${calendarEvent.id}`, calendarEvent);
                dispatch(onUpdateEvent({ ...calendarEvent, user }));
                return;

            }

            const { data } = await calendarApi.post('/events', calendarEvent);
            dispatch(onAddNewEvent({ ...calendarEvent, id: data.evento.id, user }));

        } catch (error) {
            console.log(error);
            Swal.fire('Error al guardar', error.response.data.msg, 'error');
        }

    }

    const startDeletingEvent = async () => {
    
        try {
            await calendarApi.delete(`/events/${activeEvent.id}`);
            dispatch(onDeleteEvent());

        } catch (error) {
            console.log(error)
            Swal.fire('Error al eliminar evento', error.response.data.msg, 'error');
        }

    }

    const startRefreshEvent = () => {
        dispatch(onRefreshEvent())
    }

    const startLoadingEvents = async () => {

        try {

            const { data } = await calendarApi.get('/events');
            const events = convertStringToDate(data.eventos);
            dispatch(onLoadEvents(events))

        } catch (error) {
            console.log('error cargando eventos');
            console.log(error);
        }

    }

    return {

        //* Propiedades
        events,
        activeEvent,
        hasEventSelected: !!activeEvent,

        //* Metodos
        setActiveEvent,
        startDeletingEvent,
        startLoadingEvents,
        startRefreshEvent,
        startSavingEvent,
    }
}